@use "@utils/variables" as v;

.wrapper {
    background-color: #dff9fe;
    padding-top: 5em;
    padding-bottom: 3em;
    min-height: calc(99vh - #{v.$navbarHeight});
    position: relative;

    .graphic {
        position: absolute;
        z-index: 0;
    }

    .graphic.topRight {
        top: -13px;
        right: 0;

        img {
            width: 500px;
        }
    }
    .inner {
        display: flex;
        z-index: 1;

        .image {
            display: flex;
            align-items: space-between;
            flex-direction: column;

            header {
                h1 {
                    width: 20ch;
                    text-align: center;
                    color: v.$themePurple;
                    font-weight: 700;
                }
            }
            img {
                width: 100%;
                max-width: 25em;
                margin-top: auto;
            }
            width: 45%;
        }

        form {
            width: 55%;
            max-width: 450px;
            display: flex;
            flex-direction: column;
            background-color: #4e96de;
            padding: 3em;
            border-radius: 50px;
            margin-right: 10em;

            input,
            textarea {
                margin-bottom: 1.5em;
                border: none;
                border-radius: 10px;
                height: 40px;
                padding-left: 1em;
                box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.1);
            }

            textarea {
                padding-top: 1em;
                height: 100px;
            }

            p {
                color: #fcc5c5;
                font-size: 0.8rem;
                margin: 0;
                margin-bottom: 20px;
                margin-top: -15px;
                font-weight: 500;
            }

            p.apiError {
                margin-top: 0;
            }

            p.apiSuccess {
                margin-top: 0;
                color: #00ff44;
            }

            button {
                height: 40px;
                border: none;
                margin-top: 2em;
                cursor: pointer;
                width: 80%;
                margin: 0 auto;
                background-color: v.$themePurple;
                color: white;
                border-radius: 20px;

                &:disabled {
                    background-color: rgba(1, 1, 1, 0.1);
                    color: v.$themePurple !important;
                }
            }
        }
    }
}

@media screen and (max-width: 1400px) {
    .wrapper {
        .graphic.topRight {
            img {
                width: 400px;
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .wrapper {
        .inner {
            flex-direction: column;

            .image {
                order: 1;
                margin-bottom: 4em;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                width: 100%;

                header {
                    order: 2;
                }

                img {
                    width: 250px;
                    margin-right: 3em;
                    order: 1;
                }
            }

            form {
                order: 2;
                width: 100%;
                max-width: unset;
            }
        }
    }
}

@media screen and (max-width: 900px) {
    .wrapper {
        .graphic.topRight {
            top: -6px;
            img {
                width: 200px;
            }
        }
    }
}

@media screen and (max-width: 900px) {
    .wrapper {
        .inner {
            flex-direction: column;

            .image {
                header {
                    h1 {
                        font-size: 1.5rem;
                    }
                }

                img {
                    width: 200px;

                    order: 1;
                }
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .wrapper {
        padding-top: 3em;
        .inner {
            flex-direction: column;

            .image {
                order: 1;
                flex-direction: column;
                margin-bottom: 2em;

                header {
                    order: 1;
                    margin-bottom: 2em;
                }

                img {
                    width: 250px;
                    margin-right: 0;
                    order: 2;
                }
            }

            form {
                padding: 2em;
            }
        }
    }
}
